@import '../../styles/breakpoints';

.sb-grid-area {
  // NOTE: Ensure property fallback to previous size if not set
  --sb-grid-area-medium-col-start: var(--sb-grid-area-small-col-start);
  --sb-grid-area-medium-col-span: var(--sb-grid-area-small-col-span);
  --sb-grid-area-medium-row-start: var(--sb-grid-area-small-row-start);
  --sb-grid-area-medium-row-span: var(--sb-grid-area-small-row-span);
  --sb-grid-area-large-col-start: var(--sb-grid-area-medium-col-start);
  --sb-grid-area-large-col-span: var(--sb-grid-area-medium-col-span);
  --sb-grid-area-large-row-start: var(--sb-grid-area-medium-row-start);
  --sb-grid-area-large-row-span: var(--sb-grid-area-medium-row-span);
  --sb-grid-area-wide-col-start: var(--sb-grid-area-large-col-start);
  --sb-grid-area-wide-col-span: var(--sb-grid-area-large-col-span);
  --sb-grid-area-wide-row-start: var(--sb-grid-area-large-row-start);
  --sb-grid-area-wide-row-span: var(--sb-grid-area-large-row-span);
  // NOTE: using grid as display to ensure children extend the full height of
  // the area
  display: grid;
  grid-column-end: span var(--sb-grid-area-slim-col-span);

  grid-column-start: var(--sb-grid-area-slim-col-start);
  grid-row-end: span var(--sb-grid-area-slim-row-span);
  grid-row-start: var(--sb-grid-area-slim-row-start);

  // NOTE: adding overflow auto since a column can be wider than the others if
  // content doesn't fit, thus breaking the layout.
  overflow: auto;

  @include window-width-min-small {
    grid-column-end: span var(--sb-grid-area-small-col-span);
    grid-column-start: var(--sb-grid-area-small-col-start);
    grid-row-end: span var(--sb-grid-area-small-row-span);
    grid-row-start: var(--sb-grid-area-small-row-start);
  }

  @include window-width-min-medium {
    grid-column-end: span var(--sb-grid-area-medium-col-span);
    grid-column-start: var(--sb-grid-area-medium-col-start);
    grid-row-end: span var(--sb-grid-area-medium-row-span);
    grid-row-start: var(--sb-grid-area-medium-row-start);
  }

  @include window-width-min-large {
    grid-column-end: span var(--sb-grid-area-large-col-span);
    grid-column-start: var(--sb-grid-area-large-col-start);
    grid-row-end: span var(--sb-grid-area-large-row-span);
    grid-row-start: var(--sb-grid-area-large-row-start);
  }

  @include window-width-min-wide {
    grid-column-end: span var(--sb-grid-area-wide-col-span);
    grid-column-start: var(--sb-grid-area-wide-col-start);
    grid-row-end: span var(--sb-grid-area-wide-row-span);
    grid-row-start: var(--sb-grid-area-wide-row-start);
  }
}
