@import '../../styles/breakpoints';

:root {
  --sb-grid-simple-margin: 6.25%;
  --sb-grid-detailed-margin: 4.167%;
  --sb-grid-slim-simple-gutter: var(--sbt-spacing-40);
  --sb-grid-slim-detailed-gutter: var(--sbt-spacing-20);
  --sb-grid-small-simple-gutter: var(--sbt-spacing-60);
  --sb-grid-small-detailed-gutter: var(--sbt-spacing-40);
  --sb-grid-slim-columns: 6;
  --sb-grid-small-columns: 12;
  --sb-grid-min-width: 20rem;
  --sb-grid-max-width: 100rem;
}

.sb-grid {
  --sb-grid-columns: var(--sb-grid-slim-columns);

  display: grid;
  grid-column-gap: var(--sb-grid-col-gutter);
  grid-row-gap: var(--sb-grid-row-gutter);
  grid-template-columns: repeat(var(--sb-grid-columns), 1fr);
  margin: 0 var(--sb-grid-margin);
  max-width: calc(var(--sb-grid-max-width) - var(--sb-grid-margin) * 2);
  min-width: calc(var(--sb-grid-min-width) - var(--sb-grid-margin) * 2);

  @include window-width-min-small {
    --sb-grid-columns: var(--sb-grid-small-columns);
  }
}

.sb-grid_simple {
  --sb-grid-margin: var(--sb-grid-simple-margin);
  --sb-grid-row-gutter: var(--sb-grid-slim-simple-gutter);
  --sb-grid-col-gutter: var(--sb-grid-slim-simple-gutter);

  @include window-width-min-small {
    --sb-grid-row-gutter: var(--sb-grid-small-simple-gutter);
    --sb-grid-col-gutter: var(--sb-grid-small-simple-gutter);
  }
}

.sb-grid_detailed {
  --sb-grid-margin: var(--sb-grid-detailed-margin);
  --sb-grid-row-gutter: var(--sb-grid-slim-detailed-gutter);
  --sb-grid-col-gutter: var(--sb-grid-slim-detailed-gutter);

  @include window-width-min-small {
    --sb-grid-row-gutter: var(--sb-grid-small-detailed-gutter);
    --sb-grid-col-gutter: var(--sb-grid-small-detailed-gutter);
  }
}
